<template>
    <div v-if="trade !== null" class="row">
        <div class="col-sm-12">
            <div class="card">
                <div class="card-header">
                    <h5>CARGAR EXPEDIENTE</h5>
                </div>
                <div class="card-body">
                    <div v-if="trade.tra_pdf === null">
                        <p class="text-muted">Actualmente no hay un archivo para este expediente. <a
                                href="javascript:void(0)" class="text-primary" @click="abrirModal(1)">Haga click
                                para cargar el expediente.</a></p>
                    </div>
                    <div v-else class="row">
                        <div class="col-md-3">
                            <label class="form-label">Se cargo</label>
                        </div>
                    </div>
                </div>
                <!-- Modal Nuevo Expediente-->
                <div v-if="showModal === 1" class="modal" @click="cerrarModal">
                    <div class="modal-content" @click.stop>
                        <div class="modal-header">
                            <h5 class="modal-title">Nuevo Archivo EXPEDIENTE</h5>
                        </div>
                        <div class="modal-body">
                            <div class="mb-3">
                                <label class="form-label" for="NumeroExpediente">Seleccionar Archivo</label>
                                <input type="file" ref="fileInput" requeried class="form-control"
                                    @change="onFileChange">
                            </div>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-secondary" @click="cerrarModal()">Cerrar</button>
                            <button type="button" class="btn btn-primary" @click="guardarArchivo()">Guardar</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-sm-12">
            <div class="card">
                <div class="card-header">
                    <h5>CARGAR INFORME</h5>
                </div>
                <div class="card-body">
                    <div v-if="trade.tra_pdf === null">
                        <p class="text-muted">Actualmente no hay un archivo para este expediente. <a
                                href="javascript:void(0)" class="text-primary" @click="abrirModal(2)">Haga click
                                para cargar el informe.</a></p>
                    </div>
                    <div v-else class="row">
                        <div class="col-md-3">
                            <label class="form-label">Se cargo</label>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- Modal Nuevo Informe-->
        <div v-if="showModal === 2" class="modal" @click="cerrarModal">
            <div class="modal-content" @click.stop>
                <div class="modal-header">
                    <h5 class="modal-title">Nuevo Archivo REPORTE</h5>
                </div>
                <div class="modal-body">
                    <div class="mb-3">
                        <label class="form-label" for="NumeroExpediente">Seleccionar Archivo</label>
                        <input type="file" ref="fileInput" requeried class="form-control" @change="onFileChange">
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" @click="cerrarModal()">Cerrar</button>
                    <button type="button" class="btn btn-primary" @click="guardarArchivo()">Guardar</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
import feather from "feather-icons";
import moment from 'moment';
import $ from 'jquery'; // Importa jQuery

export default {
    name: 'ArchivosOficio',
    data() {
        return {
            trade: null,
            showModal: 0,
            number: "",
            doc_tipo: "",
            id: '',
        }
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
        this.cargarDetallesOficio();
        // this.initDataTable();
    },
    async created() {
        this.id = this.$route.params.id;
    },
    methods: {
        async cargarDetallesOficio() {
            try {
                const response = await this.$getData(`trade/${this.id}`, this);
                this.trade = response.trade;
            } catch (error) {
                this.$handleApiError(error, 'cargar detalles del expediente administrativo ', this.id);
            }
        },
        async guardarArchivo() {
            if (this.selectedFile) {
                try {
                    this.$showLoadingAlert();
                    if (this.showModal === 1) {
                        this.number = this.trade.tra_number;
                        this.doc_tipo = "ADMINISTRATIVO";
                    } else {
                        this.number = this.trade.report.rep_informe + this.trade.report.rep_oficio;
                        this.doc_tipo = "INFORME";
                    }
                    const formData = {
                        file: this.selectedFile,
                        number: this.number,
                        doc_tipo: this.doc_tipo
                    }
                    console.log(formData);
                    await axios.post(
                        `${this.$store.getters.get__url}/archivos/guardarArchivoAdm`,
                        formData,
                        {
                            headers: {
                                'Content-Type': 'multipart/form-data',
                                Authorization: 'Bearer ' + this.$store.getters.get__token,
                            },
                        }
                    );
                    this.selectedFile = null;
                    this.showModal = 0;
                    this.$closeLoadingAlert();
                    this.cargarDetallesExpediente();
                } catch (error) {
                    console.error(error);
                }
            } else {
                this.$handleApiError(error, 'guardar expediente');
            }
        },

        abrirModal(num) {
            this.showModal = num;
        },

        cerrarModal() {
            this.showModal = 0;
        },

        goBack() {
            this.$router.go(-1);
        },

        onFileChange(event) {
            this.selectedFile = event.target.files[0];
            const fileInput = event.target;
            this.archivoSeleccionado = fileInput.files.length > 0;
        },
    }
}
</script>

<style scoped>
@import '@/assets/css/modal.css';
</style>