<template>
    <div>
        <div class="row d-flex align-items-start">
            <div class="col-md-4">
                <div class="card support-bar overflow-hidden">
                    <div v-if="trade != null" class="card-body pb-0">
                        <label v-if="trade.tra_state_law === 'P'" class="badge bg-light-warning mb-2 fs-6">EXPEDIENTE
                            PENDIENTE</label>
                        <label v-if="trade.tra_state_law === 'F'" class="badge bg-light-success mb-2 fs-6">EXPEDIENTE
                            ATENDIDO</label>
                        <h2 class="m-0">{{ trade.tra_number }}</h2>
                        <span class="text-primary">{{ trade.tra_name }}</span>
                        <p class="mb-3 mt-3">{{ trade.tra_matter }}</p>
                        <div class="col-lg-12 mb-2">
                            <div class="d-flex align-items-center p-2 bg-light rounded">
                                <div
                                    class="d-flex align-items-center justify-content-center bg-primary rounded-pill p-2 text-white">
                                    <i data-feather="file-text"></i>
                                </div>
                                <div class="px-2"><span class="fw-bold">Área
                                        de origen:</span> {{ trade.area.are_name }}</div>
                            </div>
                        </div>
                        <div class="col-lg-12 mb-2">
                            <div class="d-flex align-items-center p-2 bg-light rounded">
                                <div
                                    class="d-flex align-items-center justify-content-center bg-primary rounded-pill p-2 text-white">
                                    <i data-feather="file-text"></i>
                                </div>
                                <div class="px-2"><span class="fw-bold">Referencias -> </span>{{
                                    trade.type_reference.type_name }}</div>
                            </div>
                        </div>
                        <div class="col-lg-12 mb-2" v-if="trade.tra_exp_ext != null">
                            <div class="d-flex align-items-center p-2 rounded">
                                <div class="px-2"><span class="fw-bold"></span> {{ trade.tra_exp_ext }}</div>
                            </div>
                        </div>
                        <div class="col-lg-12 mb-2" v-if="trade.tra_doc_recep != null">
                            <div class="d-flex align-items-center p-2 rounded">
                                <div class="px-2"><span class="fw-bold"></span> {{ trade.tra_doc_recep }}</div>
                            </div>
                        </div>
                    </div>
                    <hr class="mb-0">
                    <div v-if="trade != null" class="card-footer border-0">
                        <div class="row text-center">
                            <div class="col">
                                <h4 class="m-0">{{ formatDate(trade.tra_arrival_date) }}</h4>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="trade != null" class="col-md-4 ">
                <div v-if="trade.report != null" class="card support-bar overflow-hidden">
                    <div v-if="trade != null" class="card-body pb-4">
                        <label class="badge bg-light-primary mb-2 fs-6">REPORTE</label>
                        <div class="col-lg-12 mb-2">
                            <div class="d-flex align-items-center p-2 bg-light rounded">
                                <div
                                    class="d-flex align-items-center justify-content-center bg-primary rounded-pill p-2 text-white">
                                    <i data-feather="file-text"></i>
                                </div>
                                <div class="px-2"><span class="fw-bold">Oficio:</span> {{ trade.report.rep_oficio }}
                                </div>
                            </div>
                        </div>
                        <div class="col-lg-12 mb-2">
                            <div class="d-flex align-items-center p-2 bg-light rounded">
                                <div
                                    class="d-flex align-items-center justify-content-center bg-primary rounded-pill p-2 text-white">
                                    <i data-feather="file-text"></i>
                                </div>
                                <div class="px-2"><span class="fw-bold">Informe:</span> {{ trade.report.rep_informe }}
                                </div>
                            </div>
                        </div>
                        <div class="col-lg-12 mb-2">
                            <div class="d-flex align-items-center p-2 bg-light rounded">
                                <div
                                    class="d-flex align-items-center justify-content-center bg-primary rounded-pill p-2 text-white">
                                    <i data-feather="file-text"></i>
                                </div>
                                <div class="px-2"><span class="fw-bold">Derivado a:</span> {{
                                    trade.report.area.are_name }}</div>
                            </div>
                        </div>
                        <div class="col-lg-12 mb-2">
                            <div class="d-flex align-items-center p-2 bg-light rounded">
                                <div
                                    class="d-flex align-items-center justify-content-center bg-primary rounded-pill p-2 text-white">
                                    <i data-feather="file-text"></i>
                                </div>
                                <div class="px-2"><span class="fw-bold">Fecha:</span> {{
                                    formatDate(trade.report.created) }}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-else="trade.report == null" class="card support-bar overflow-hidden">
                    <div v-if="trade != null" class="card-body pb-4">
                        <label class="badge bg-light-primary mb-4 fs-6">REGISTRAR REPORTE</label>
                        <div class="form-group">
                            <label class="form-label">N° Oficio</label>
                            <input type="text" class="form-control" v-model="datosgenerales.rep_oficio"
                                @input="validarCampo('rep_oficio')" :class="{ 'has-error': errores.rep_oficio }" />
                            <span class="error-message" v-if="errores.rep_oficio">Ingrese el número
                                de oficio</span>
                        </div>
                        <div class="form-group">
                            <label class="form-label">N° Informe</label>
                            <input type="text" class="form-control" v-model="datosgenerales.rep_informe"
                                @input="validarCampo('rep_informe')" :class="{ 'has-error': errores.rep_informe }" />
                            <span class="error-message" v-if="errores.rep_informe">Ingrese el número
                                de informe</span>
                        </div>
                        <div class="form-group">
                            <label class="form-label">Derivar a la oficina:</label>
                            <select class="form-control" v-model="datosgenerales.rep_area"
                                :class="{ 'has-error': errores.rep_area }" @change="validarCampo('rep_area')">
                                <option value="" disabled selected class="py-5">-- Seleccione una opción
                                </option>
                                <option v-for="(area) in areas" :key="area.are_id" :value="area.are_id" class="py-3">
                                    {{ area.are_name }}
                                </option>
                            </select>
                            <span class="error-message" v-if="errores.rep_area">Seleccione una
                                Ocifina.</span>
                        </div>
                        <button type="button" class="btn btn-primary btn-sm" @click="registrar">REGISTRAR</button>
                    </div>
                </div>
            </div>
            <div v-if="trade != null" class="col-md-4">
                <div class="card feed-card">
                    <div class="card-header py-3">
                        <span class="fw-bold">RESPONSABLE</span>
                    </div>
                    <div class="card-body py-3">
                        <div class="mb-3"><i class="material-icons-two-tone text-primary"><span
                                    class="material-symbols-outlined">
                                    gavel
                                </span></i> {{ trade.lawyer.persona.nat_nombres }} {{
                                    trade.lawyer.persona.nat_apellido_paterno }}
                            {{ trade.lawyer.persona.nat_apellido_materno }}
                        </div>
                    </div>
                </div>
                <div class="card feed-card">
                    <div class="card-header py-3">
                        <span class="fw-bold">DIAS DE RETRASO</span>
                    </div>
                    <div class="card-body py-3">
                        <div v-if="trade.report === null" class="row rounded">
                            <span>El expediente lleva sin atención <label class="badge bg-light-danger mb-4 fs-6">{{dias}} días</label></span>
                        </div>
                        <div v-if="trade.report !== null" class="row rounded">
                            <span>El expediente a sido atendido</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import feather from "feather-icons";
import DataTable from '@/utils/dataTable';
import { getData, postData } from '@/utils/fetchData';
import moment from 'moment';

export default {
    mixins: [DataTable],
    data() {
        return {
            dias: "",
            areas: [],
            dataTable: null,
            trade: null,
            persons: [],
            formattedDate: "",
            id: '',
            datosgenerales: {
                rep_informe: "",
                rep_oficio: "",
                rep_area: "",
            },
            errores: {
                rep_informe: false,
                rep_oficio: false,
                rep_area: false
            }
        };
    },
    created() {
        this.sector = this.$store.getters.get__sector;
        this.id = this.$route.params.id;
        this.cargarDetallesOficio();
        this.listarAreas();
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    methods: {
        async cargarDetallesOficio() {
            try {
                const response = await this.$getData(`trade/${this.id}`, this);
                this.trade = response.trade;
                this.dias = this.diasRetraso(this.trade.created_at);
                this.persons = this.trade.persons;
                this.datosgenerales.rep_informe = this.getIniciales();
                this.datosgenerales.rep_oficio = this.getIniciales();
            } catch (error) {
                this.$handleApiError(error, 'cargar detalles del expediente administrativo ', this.id);
            }
        },

        async listarAreas() {
            try {
                const result = await getData('area', this);
                this.areas = result.data;
            } catch (error) {
                this.$handleApiError(error, 'listar areas');
            } finally {
                this.$closeLoadingAlert();
            }
        },

        async registrar() {
            if (this.validarCampos()) {
                try {
                    this.loader = true;
                    const formData = {
                        rep_oficio: this.datosgenerales.rep_oficio,
                        rep_informe: this.datosgenerales.rep_informe,
                        rep_tra_id: this.trade.tra_id,
                        rep_are_id: this.datosgenerales.rep_area
                    }
                    const response = await this.$postData('report/create', this, formData);
                    if (response.state === 'success') {
                        window.location.reload();
                    }
                } catch (error) {
                    // Captura y maneja el error aquí
                    console.error('Error en la función registrar:', error);

                    // Puedes mostrar mensajes de error o realizar otras acciones según tus necesidades
                    if (error.response) {
                        console.error('Respuesta del servidor:', error.response.data);
                    } else if (error.message) {
                        console.error('Mensaje de error:', error.message);
                    }

                    this.$handleApiError(error, 'crear informe');
                }
            } else {
                console.log('error');
            }
        },

        diasRetraso(fechaCreacion) {
            // Convertir la fecha de creación a objeto Date
            var fechaCreacionObj = new Date(fechaCreacion);
            // Obtener la fecha y hora actual
            var fechaActual = new Date();
            // Calcular la diferencia entre las fechas en milisegundos
            var diferenciaMilisegundos = fechaActual - fechaCreacionObj;
            // Calcular el número de días redondeando hacia abajo la diferencia en milisegundos
            var diasPasados = Math.floor(diferenciaMilisegundos / (1000 * 60 * 60 * 24));
            return diasPasados;
        },

        getIniciales() {
            const nombres = this.trade.lawyer.persona.nat_nombres + ' ' + this.trade.lawyer.persona.nat_apellido_paterno + ' ' + this.trade.lawyer.persona.nat_apellido_materno;
            const palabras = nombres.split(" ");
            let iniciales = "";
            // Iterar sobre cada palabra
            palabras.forEach(palabra => {
                // Verificar si la palabra no está vacía
                if (palabra.length > 0) {
                    // Obtener la primera letra de la palabra y agregarla a las iniciales
                    iniciales += palabra.charAt(0).toUpperCase();
                }
            });

            //Año actual
            var fechaActual = new Date();
            var añoActual = fechaActual.getFullYear();

            return añoActual + '-UNPRG-OAJ-' + iniciales;

        },

        validarCampo(campo) {
            this.errores[campo] = this.datosgenerales[campo] === "";
        },

        validarCampos() {
            Object.keys(this.errores).forEach(propiedadError => {
                this.validarCampo(propiedadError, propiedadError);
            });
            return !Object.values(this.errores).some(error => error);
        },

        formatDate(date) {
            moment.locale('es');
            return moment(date).format('DD MMM YYYY');
        },
        goBack() {
            this.$router.go(-1);
        },
    },
};
</script>

<style scoped>
.error-message {
    /* padding-left: 15px; */
    color: #EA4D4D;
    font-size: 12px;
}
</style>